}

header {
  position: relative;
}

header nav {
  width: 100%;
  height: 100px;
  background-color: #fff8dc;
  border-bottom: 1px solid #5f5c5c;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
}

header nav .logo {
  align-items: center;
  margin-left: 2em;
  display: flex;
}

header nav .logo .fa-solid {
  color: red;
  font-size: 3em;
}

header nav .logo span {
  color: red;
}

header nav .logo h2 {
  margin-left: .5em;
  font-family: Lobster, cursive;
  font-size: 3em;
}

header nav .navigation {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

header nav .navigation .menu {
  z-index: 5;
  justify-content: center;
  align-items: center;
  transition: all .5s;
  display: flex;
}

header nav .navigation .menu li {
  list-style-type: none;
}

header nav .navigation .menu li a {
  color: #000;
  text-transform: uppercase;
  padding: 40px 25px;
  font-size: 25px;
  line-height: 1;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

header nav .navigation .menu li a:hover {
  font-weight: bolder;
  box-shadow: inset 0 -5px red, inset 700px 0 #24222208;
}

header nav .navigation .hambg {
  width: 30px;
  height: 4px;
  cursor: pointer;
  z-index: 2;
  background: #000;
  border-radius: 10px;
  transition: all .3s;
  position: relative;
}

header nav .navigation .hambg:before, header nav .navigation .hambg:after {
  content: "";
  height: 4px;
  background: #000;
  border-radius: 10px;
  transition: all .3s;
  position: absolute;
  right: 0;
}

header nav .navigation .hambg:before {
  width: 20px;
  top: -10px;
}

header nav .navigation .hambg:after {
  width: 25px;
  top: 10px;
}

header nav .navigation .toggle-menu {
  width: 30px;
  height: 100%;
  z-index: 3;
  cursor: pointer;
  opacity: 0;
  position: absolute;
}

header nav .navigation .hambg, header nav .navigation .toggle-menu {
  display: none;
}

header nav .navigation input:checked ~ .hambg {
  background: none;
}

header nav .navigation input:checked ~ .hambg:before {
  width: 30px;
  top: 0;
  transform: rotate(-45deg);
}

header nav .navigation input:checked ~ .hambg:after {
  width: 30px;
  top: 0;
  transform: rotate(45deg);
}

header nav .navigation input:checked ~ .menu {
  right: 0;
  box-shadow: -20px 0 40px #0000004d;
}

